import React, { useRef, useState } from "react";
// fonntawesome
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCaretLeft } from "@fortawesome/free-solid-svg-icons";
import { faCaretRight } from "@fortawesome/free-solid-svg-icons";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";
import { GnbCss } from "./GnbCss";
import { Link } from "react-router-dom";
import type { GnbDataType } from "./data/GnbData";
import { GnbData } from "./data/GnbData";
// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
// import required modules
import { Pagination, Navigation, Keyboard } from "swiper";
import { Button } from "utils/repeat";

export interface toggle {
  showGnb: boolean;
}

const Gnb = ({ showGnb }: toggle) => {
  const [swiperRef, setSwiperRef] = useState<any>(null);

  const slideTo = (i: number) => {
    swiperRef.slideTo(i - 1, 300);
  };

  const navigateNext = () => {
    swiperRef.slideNext();
  };

  const navigatePrev = () => {
    swiperRef.slidePrev();
  };

  return (
    <>
      <GnbCss>
        <div className={`workWrap${showGnb ? " show wow fadeIn" : ""}`}>
          <div className="work">
            <span>  </span>
          </div>
          <button
            className="swiper-button-prev arrowBtn"
            onClick={navigatePrev}
          >
            <FontAwesomeIcon icon={faCaretLeft} />
          </button>
          <Swiper
            pagination={{
              type: "fraction",
            }}
            keyboard={true}
            slidesPerView={1}
            spaceBetween={30}
            modules={[Pagination, Keyboard]}
            centeredSlides={true}
            navigation={{
              prevEl: ".swiper-button-prev",
              nextEl: ".swiper-button-next",
            }}
            onSwiper={setSwiperRef}
            className="mySwiper"
          >
            <SwiperSlide>
              <Link to="/sutabucks"></Link>
            </SwiperSlide>
            <SwiperSlide>
              <Link to="/ggobook"></Link>
            </SwiperSlide>
            <SwiperSlide>
              <Link to="/methedu">metaedu</Link>
            </SwiperSlide>
            <SwiperSlide></SwiperSlide>
            <SwiperSlide></SwiperSlide>
            <SwiperSlide></SwiperSlide>
            <SwiperSlide></SwiperSlide>
          </Swiper>
          <button
            className="swiper-button-next arrowBtn"
            onClick={navigateNext}
          >
            <FontAwesomeIcon icon={faCaretRight} />
          </button>
          <p className="swiperButton">
            {GnbData.map((v, i) => {
              return (
                <Button key={i}>
                  <button
                    onClick={() => {
                      slideTo(v.id);
                    }}
                  >
                    {v.name}
                  </button>
                </Button>
              );
            })}
          </p>
        </div>
      </GnbCss>
    </>
  );
};

export default Gnb;
